'use client'

import { useEffect, useState } from 'react'
import { useRouter } from 'next/navigation'
import Link from 'next/link'
import { isAuthenticated, getProducts, type Product } from '@/lib/storage'
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card'
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table'
import { Badge } from '@/components/ui/badge'
import { Alert, AlertDescription } from '@/components/ui/alert'

export default function StockReportPage() {
  const [products, setProducts] = useState<Product[]>([])
  const [stockStats, setStockStats] = useState({
    totalProducts: 0,
    outOfStock: 0,
    lowStock: 0,
    inStock: 0,
    totalValue: 0
  })
  const router = useRouter()

  useEffect(() => {
    if (!isAuthenticated()) {
      router.push('/login')
      return
    }
    loadStockData()
  }, [router])

  const loadStockData = () => {
    const allProducts = getProducts()
    setProducts(allProducts)

    // Calculate statistics
    const stats = {
      totalProducts: allProducts.length,
      outOfStock: allProducts.filter(p => p.stok === 0).length,
      lowStock: allProducts.filter(p => p.stok > 0 && p.stok < 10).length,
      inStock: allProducts.filter(p => p.stok >= 10).length,
      totalValue: allProducts.reduce((sum, p) => sum + (p.harga * p.stok), 0)
    }
    setStockStats(stats)
  }

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('id-ID', {
      style: 'currency',
      currency: 'IDR',
      minimumFractionDigits: 0
    }).format(amount)
  }

  const getStockStatus = (stock: number) => {
    if (stock === 0) return { label: 'Habis', variant: 'destructive' as const }
    if (stock < 10) return { label: 'Stok Rendah', variant: 'secondary' as const }
    return { label: 'Tersedia', variant: 'default' as const }
  }

  const getStockBadge = (stock: number) => {
    const status = getStockStatus(stock)
    return <Badge variant={status.variant}>{status.label}</Badge>
  }

  const sortedProducts = [...products].sort((a, b) => {
    // Sort by stock status: out of stock first, then low stock, then in stock
    if (a.stok === 0 && b.stok > 0) return -1
    if (a.stok > 0 && b.stok === 0) return 1
    if (a.stok < 10 && b.stok >= 10) return -1
    if (a.stok >= 10 && b.stok < 10) return 1
    return a.nama.localeCompare(b.nama)
  })

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="border-b bg-card">
        <div className="container mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <Link href="/dashboard" className="text-muted-foreground hover:text-foreground">
                ‚Üê Kembali ke Dashboard
              </Link>
              <h1 className="text-2xl font-bold mt-2">Laporan Stok Barang</h1>
            </div>
          </div>
        </div>
      </header>

      <main className="container mx-auto px-4 py-8">
        {/* Hero Image */}
        <div className="mb-8">
          <img 
            src="https://placehold.co/1200x200?text=Laporan+Stok+Barang+Real+Time" 
            alt="Laporan Stok Barang Real Time" 
            onError={(e) => { e.currentTarget.style.display='none'; }} 
            className="w-full rounded-lg object-cover" 
          />
        </div>

        {/* Stock Statistics */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-5 gap-6 mb-8">
          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Total Produk
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stockStats.totalProducts}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Stok Habis
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-red-600">{stockStats.outOfStock}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Stok Rendah
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-orange-600">{stockStats.lowStock}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Stok Aman
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold text-green-600">{stockStats.inStock}</div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                Nilai Total Stok
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-lg font-bold">{formatCurrency(stockStats.totalValue)}</div>
            </CardContent>
          </Card>
        </div>

        {/* Alerts for Critical Stock */}
        {stockStats.outOfStock > 0 && (
          <Alert variant="destructive" className="mb-6">
            <AlertDescription>
              <strong>Perhatian!</strong> Ada {stockStats.outOfStock} produk yang stoknya habis. 
              Segera lakukan pembelian untuk menghindari kehilangan penjualan.
            </AlertDescription>
          </Alert>
        )}

        {stockStats.lowStock > 0 && (
          <Alert className="mb-6">
            <AlertDescription>
              <strong>Peringatan!</strong> Ada {stockStats.lowStock} produk dengan stok rendah (kurang dari 10). 
              Pertimbangkan untuk menambah stok.
            </AlertDescription>
          </Alert>
        )}

        {/* Stock Report Table */}
        <Card>
          <CardHeader>
            <CardTitle>Detail Stok Produk</CardTitle>
          </CardHeader>
          <CardContent>
            {products.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-muted-foreground">Belum ada produk untuk dilaporkan.</p>
                <Link href="/products" className="text-primary hover:underline mt-2 inline-block">
                  Tambah produk pertama Anda
                </Link>
              </div>
            ) : (
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>Nama Produk</TableHead>
                      <TableHead>Harga Satuan</TableHead>
                      <TableHead>Stok</TableHead>
                      <TableHead>Status</TableHead>
                      <TableHead>Nilai Stok</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {sortedProducts.map((product) => (
                      <TableRow key={product.id}>
                        <TableCell className="font-medium">{product.nama}</TableCell>
                        <TableCell>{formatCurrency(product.harga)}</TableCell>
                        <TableCell>
                          <span className={`font-medium ${
                            product.stok === 0 ? 'text-red-600' : 
                            product.stok < 10 ? 'text-orange-600' : 
                            'text-green-600'
                          }`}>
                            {product.stok}
                          </span>
                        </TableCell>
                        <TableCell>{getStockBadge(product.stok)}</TableCell>
                        <TableCell>{formatCurrency(product.harga * product.stok)}</TableCell>
                      </TableRow>
                    ))}
                  </TableBody>
                </Table>
              </div>
            )}
          </CardContent>
        </Card>

        {/* Summary */}
        {products.length > 0 && (
          <Card className="mt-6">
            <CardHeader>
              <CardTitle>Ringkasan</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4 text-sm">
                <div>
                  <p><strong>Total Produk:</strong> {stockStats.totalProducts} item</p>
                  <p><strong>Produk Tersedia:</strong> {stockStats.inStock} item</p>
                  <p><strong>Produk Stok Rendah:</strong> {stockStats.lowStock} item</p>
                  <p><strong>Produk Habis:</strong> {stockStats.outOfStock} item</p>
                </div>
                <div>
                  <p><strong>Nilai Total Inventori:</strong> {formatCurrency(stockStats.totalValue)}</p>
                  <p><strong>Rata-rata Nilai per Produk:</strong> {
                    stockStats.totalProducts > 0 
                      ? formatCurrency(stockStats.totalValue / stockStats.totalProducts)
                      : formatCurrency(0)
                  }</p>
                </div>
              </div>
            </CardContent>
          </Card>
        )}
      </main>
    </div>
  )
}
